@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap);
/* base styles */
html, body, #root {
  height: 100%;
}
body {
  font-family: Raleway, sans-serif;
  margin: 0;
  font-size: 1.4em;
  background: #dfdfdf;
  color: #333;
}
h1,h2,h3,p {
  margin: 0;
}
input, textarea {
  color: #333;
  padding: 8px;
  font-size: 1em;
  border-radius: 4px;
  border: 1px solid #d4d4d4;
  background-color: #fff;
  display: block;
  width: 100%;
  box-sizing: border-box;
}
.page-title {
  text-align: center;
  margin: 40px auto;
  color: #333;
}
.loading, .error {
  text-align: center;
  margin: 40px auto;
}
.App {
  min-height: 100%;
  margin-top: 30px;
}
.recipe__single{box-shadow:10px 5px 10px rgba(0,0,0,.25);border:1px solid rgba(0,0,0,.25);padding:2rem;border-radius:6px}.recipe__title{font-size:1.6rem}.recipe__time{font-size:1.2rem}.recipe__description{font-size:1rem;margin-top:1rem}
.recipe__grid{display:grid;grid-template-columns:repeat(3, 1fr);grid-gap:30px;gap:30px}
.create__label{display:block;margin-bottom:1rem}.create__input{outline:none}.create__ingredients{display:flex;align-items:center;justify-content:space-between}.create__ingredients button{margin-left:2rem}.create__input{margin-top:.5rem}
.nav{margin-bottom:30px;background:#6495ed}.nav__logo{text-decoration:none;font-size:2rem;color:#fff}.nav__content{display:flex;align-items:center;justify-content:space-between;padding:1rem 0;color:#fff}
