.recipe {
    &__single {
        box-shadow: 10px 5px 10px rgba(0, 0, 0, 0.25);
        border: 1px solid rgba(#000,0.25);
        padding: 2rem;
        border-radius: 6px;
    }

    &__title {
        font-size: 1.6rem;
    }
    &__time {
        font-size: 1.2rem;
    }
    &__description {
        font-size: 1rem;
        margin-top: 1rem;
    }
}
